.main-container {
    margin-top: 65px;
    border: 1px solid #dedede;
    border-radius: 5px;
    height: calc(100vh - 105px);
    margin-bottom: 2px;
}

footer {
    background: black;
    height: 33px;
    padding-top: 3px;  
}

footer span {
    vertical-align: middle;
    color: #FFF;
}

.header-profile-icon {
    float: right;
    margin-top: 10px;
    cursor: pointer;
}

.logout-container {
    padding: 10px;
}

.dashbord-container {
    padding-left: 5px !important;
    padding-right: 5px !important;
}

@media (max-width: 960px) {
    .tab-panel{
        width: 94% !important;
    }
}

.tab-panel {
    width: 98%;
    height: calc(100vh - 200px);
    overflow-y: auto;
    border-radius: 10px;
    border: 0.2px solid #f5f5f5;
    box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);
}

.app-bar-header {
    background-color: #FFF !important;
    border-radius: 10px;
}